import React from "react";

function SelectorN100() {
  // Create an array from 1 to 100
  const numbers = Array.from({ length: 100 }, (_, i) => i + 1);

  // Shuffle the array using Fisher-Yates
  const shuffledNumbers = [...numbers]; // copy to avoid mutating original
  for (let i = shuffledNumbers.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1));
    [shuffledNumbers[i], shuffledNumbers[j]] = [
      shuffledNumbers[j],
      shuffledNumbers[i],
    ];
  }

  return (
    <div className="flex h-full w-full border-2 border-white bg-black justify-center items-center">
      {/* Container for all squares */}
      <div className="grid grid-cols-10 gap-2 w-[500px] h-[500px] p-2 bg-white">
        {shuffledNumbers.map((num) => (
          <button
            key={num}
            className="w-[40px] h-[40px] bg-gray-300 cursor-pointer border-2 border-black flex justify-center items-center text-sm font-bold"
          >
            {num}
          </button>
        ))}
      </div>
    </div>
  );
}

export default SelectorN100;
